import React from 'react';

function Card(props) {
    return (
        <div className="bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5">
            <img src={`https://robohash.org/${props.data.id}?size=200x200`} alt="rob"></img>
            <div>
                <h2>{props.data.name}</h2>
                <p>{props.data.email}</p>
            </div>

        </div>
    )
}
export default Card;